// Root colors
:root {
    --primary-white-background: #F5F5F7;
    --primary-black-background: #000000;

    --primary-white-Text-color: #D0D0D0;
    --primary-black-Text-color: #1D1D1F;

    --primary-good-alert-color: rgb(30, 215, 96);
    --primary-error-alert-color: rgb(255, 0, 0);

    --primary-white-button-background: rgba(208, 208, 208, 0.55);
}

.popular-products-container {
    max-width: 600px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 16px;
    margin-top: 50px;

    .popular-products-contents {
        max-width: 568px;
        width: 100%;
        height: auto;
        padding-bottom: 45px;
        background: #000000;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
        backdrop-filter: blur(25px);
        border-radius: 40px;
        margin: 0 auto;

        .popular-products-head-text {
            padding-top: 45px;
            padding-left: 30px;

            h1 {
                font-style: normal;
                font-weight: 700;
                font-size: 1.875rem;
                color: var(--primary-white-Text-color);
            }
        }

        .popular-products-cards {
            padding: 0 35px 0;

            .card {
                max-width: 438px;
                width: 100%;
                height: 214px;
                background: rgba(29, 29, 31, 0.55);
                margin: 46px auto;
                // margin-left: 65px;
            }

            .extra-cards {
                display: none;
            }
        }

        .next-previous {
            display: flex;
            justify-content: center;

            .next {
                width: 38px;
                height: 36px;
                background: rgba(29, 29, 31, 0.55);
                backdrop-filter: blur(2px);
                border-radius: 20px;
                display: flex;
                justify-content: center;
                align-items: center;
                margin-right: 10px;
                color: var(--primary-white-Text-color);
            }

            .previous {
                width: 38px;
                height: 36px;
                background: rgba(29, 29, 31, 0.55);
                backdrop-filter: blur(2px);
                border-radius: 20px;
                display: flex;
                justify-content: center;
                align-items: center;
                margin-right: 15px;
            }

            .see-all {
                max-width: 88px;
                width: 100%;
                height: 36px;
                background: rgba(29, 29, 31, 0.55);
                backdrop-filter: blur(2px);
                border-radius: 10px;
                display: flex;
                justify-content: center;
                align-items: center;

                p {
                    color: var(--primary-white-Text-color);
                }
            }
        }
    }

}

// Media quries start

@media only screen and (min-width: 600px) {

    .popular-products-container {

        max-width: 1537px;
        width: 100%;

        .popular-products-contents {
            max-width: 1320px;
            width: 100%;
            height: 693px;
            padding-top: 20px;

            .popular-products-head-text {
                margin-left: 20px;

                h1 {
                    font-size: 45px;
                }
            }

            .popular-products-cards {
                display: grid;
                grid-template-columns: 1fr 1fr 1fr;
                grid-template-rows: 214px 214px;
                grid-gap: 20px 0;

                .extra-cards {
                    display: block;
                }

                .card {
                    width: 315px;
                    height: 214px;

                    &:nth-child(2){
                        margin-top: 150px;
                    }
                    &:nth-child(3), &:nth-child(5) {
                        margin-top: -39px;
                    }

    
                }

                .left-moving-card{
                    grid-row-start: 2;
                    grid-row-end: 2;

                    grid-column-start: 3;
                    grid-column-end: 3;
                }
            }

            .next-previous {
                display: flex;
                justify-content: flex-end;
                padding-right: 84px;
                margin-top: 10px;
            }
        }

    }
}